html, body {
  margin: 0px;
  height: 100%;
  width: 100%;
}
@media only screen and (min-width: 500px) {
	body{
    background-image: url(static/backgrounds/hiatus/hiatusBackground.svg);
	}	
}
body {
  height: 100%;
  width: 100%;
  background-size: cover;
  background-color: #596b5d;
}

@media only screen and (max-width: 450px) {
	body{
    background-position-y: bottom;
	}	
}

#root {
  height: 100%;
  width: 100%;
}